
import Home from './views/Home.vue'

import {createRouter, createWebHistory} from "vue-router";


const routes = [
  {
    path: '/',
    redirect: '/en'
  },
      {
        path: '/:lang/',
        name: 'Home',
        component: Home
      },

      {
        path: '/login',
        name: 'Login',
        component: () => import('./views/Login.vue')
      },
    //   {
    //     path: '/assets/',
    //   },
    //   {
    //     path: '/BooksDetails/:id',
    //     name: 'BooksDetails',
    //     component: () => import('../views/BooksDetails.vue')
    //   },
    //   {
    //     path: '/userPage/:id',
    //     name: 'userPage',
    //     component: () => import('../views/userPage.vue')
    //   }
    
];


const router = createRouter({
  history: createWebHistory(),
  routes
});

router.beforeEach((to, from, next) => {
  
  let currentStorageLang;

  if (localStorage.getItem("i18n") !== null) {
    currentStorageLang = JSON.parse(window.localStorage.getItem("i18n"));
  }

  if(currentStorageLang !== to.params.lang) {
    window.localStorage.setItem('i18n', JSON.stringify(to.params.lang));
    window.location.reload(true);
  } else {
    next();
  }

 
})

export default router