<template>
  <h1>{{ lang }}</h1>
  <navBar />
  <div class="container">
    <router-view></router-view>
  </div>
</template>

<script>
import navBar from "./components/navBar.vue";

export default {
  name: "App",
  components: {
    navBar,
  },
  mounted() {
    this.setLanguage();
  },
  methods: {
    setLanguage() {
      let langInStorage = JSON.parse(window.localStorage.getItem("i18n")) || "en";
      this.$i18n.locale = langInStorage;
    },
  },
};
</script>

