import axios from 'axios';

const url = 'api/books/'


class BooksService {
    static getAllBooks() {
        
        return new Promise((resolve, reject) => {
            axios.get(url).then(function(res) {
                    const resData = res.data;
                    resolve(resData);
                }).catch(function (error) {
                    reject(error)
                })
        })

        
    }
}

export default BooksService;